@font-face {
  font-family: "Heebo";
  font-weight: 400;
  src: url("@assets/fonts/Heebo-Regular.ttf");
}
@font-face {
  font-family: "Heebo";
  font-weight: 500;
  src: url("@assets/fonts/Heebo-Medium.ttf");
}
@font-face {
  font-family: "Heebo";
  font-weight: 700;
  src: url("@assets/fonts/Heebo-Bold.ttf");
}
@font-face {
  font-family: "Heebo";
  font-weight: 900;
  src: url("@assets/fonts/Heebo-Black.ttf");
}
*,
*::after,
*::before {
  box-sizing: border-box;
  z-index: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

body {
  margin: 0;
  font-family: "Heebo", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #21243D;
}